import React, { useEffect, useState } from "react";
import { Route } from "react-router-dom";
import { gsap } from "gsap";
import "./styles/App.scss";
import Header from "./components/header";
import Navigation from "./components/navigation";

import AboutMe from "./pages/aboutme.js";
import Blog from "./pages/blog";
import Services from "./pages/services";
import Packages from "./pages/packages";
import Home from "./pages/home";
import Post1 from "./pages/Post1";
import Post2 from "./pages/Post2";
import Post3 from "./pages/Post3";


// import Post2 from "./pages/Post2"


const routes = [
  { path: "/", name: "Home", Component: Home },
  { path: "/aboutme", name: "aboutme", Component: AboutMe },
  { path: "/blog", name: "blog", Component: Blog },
  { path: "/contact", name: "contact", Component: Services },
  { path: "/packages", name: "packages", Component: Packages },
  { path: "/blog/post1", name: "post1", Component: Post1 },
  { path: "/blog/post2", name: "post2", Component: Post2 },
  { path: "/blog/post3", name: "post3", Component: Post3 }


];

function debounce(fn, ms) {
  let timer;
  return () => {
    clearTimeout(timer);
    timer = setTimeout(() => {
      timer = null;
      fn.apply(this, arguments);
    }, ms);
  };
}

function App() {
  const [dimensions, setDimensions] = React.useState({
    height: window.innerHeight,
    width: window.innerWidth
  });

  useEffect(() => {
    // prevents flashing
    gsap.to("body", 0, { css: { visibility: "visible" } });
    const debouncedHandleResize = debounce(function handleResize() {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth
      });
    }, 1000);

    window.addEventListener("resize", debouncedHandleResize);
    return () => {
      window.removeEventListener("resize", debouncedHandleResize);
    };
  });



  return (
    <>
      <Header dimensions={dimensions} />
      <div className='App'>
        {routes.map(({ path, Component }) => (
          <Route key={path} exact path={path}>
            <Component dimensions={dimensions} />
          </Route>
        ))}
      </div>
      <Navigation />
    </>
  );
}

export default App;
