import React from 'react'
import claude from '../assets/post1/claude.jpg';
import colors from '../assets/post1/colors.gif';
import newdude from '../assets/post1/newdude.jpg';
import quote1 from '../assets/post1/quote1.jpg';
import quote2 from '../assets/post1/quote2.jpg';
import quote3 from '../assets/post1/quote3.jpg';
import quote4 from '../assets/post1/quote4.jpg';
import erik from '../assets/post1/erik.jpg';
import { NavLink } from "react-router-dom";









const Post1 = () => {
    return (
        <div className="page">
            <div className="container">
                <div className="row">
                    <div className="post_container">
                        <div className="post_header">
                        <h2>Impressionism: The Late 19th century Avant-garde </h2>
                        </div>
                        <div className="post_content">
                        I have always been impressed by French culture. Regardless of the epoch, in my opinion, French art is somehow always classy. One particular art movement has always inspired me and still drives me back to it - Impressionism. Despite the fact that it originated in the late 19th century, Impressionism is a movement which characteristics influence many artists even now.
                        </div>
                        <div className="image_container">
                            <img src={colors} alt="colors"/>
                        </div>
                        <div className="post_header">
                        <h2>New Wave of Light</h2>
                        </div>
                        <div className="post_content">
                        Isaac Newton explained how light worked and how white light was composed of colors. But at the beginning of the 19th century the nature of light became a controversial topic among French scientists. After 200 years, new wave theory of light by Augustin-Jean Fresnel was confirmed. Ethan Siegel, author of the article The Greatest Mistake in the History of Physics on Forbes, explains the process of the new wave theory of light.
                        Back then, in the late 19th century, the great experiment and the theory didn’t leave behind the group of French artists. Painting and experimenting with the light and colors on canvas are the main focus and the only motive of the upcoming movement. Artists were guided by impression – impression of the beauty of nature, landscapes and different atmosphere. Hence the old was rejected - romanticism and neoclassicism attributes were highly avoided. 
                        On the other side, so many other cultures were anyway not explored enough. But what was beyond the borders of Europe? The most fascinated and mysterious to them – Far East.  So, by exploring the other ways of living, believing and by exploring other traditions, it made a huge impact on the new wave in the arts. Paintings, poems, music etc. were created by an impression of all of it.
                        </div>

                        <div className="image_container">
                            <img src={quote2} alt="quote2"/>
                        </div>

                    <div className="post_header">
                        <h2>The Very Beginning</h2>
                    </div>

                    <div className="post_content">
                    A group of rejected artists created their own society and thus made independent exhibitions. Their particular artistic expression was the opposite of what Parisian audiences were used to during that time.  Features of their art works were freely and individually expressed, using various painting techniques. It made their style quite out-of-the-box, and therefor rebellious. 
                    Among many other artists from the organization, I will single out painters Claude Monet, Edgar Degas known as the painter of dancers and Camille Pissarro. I would recommend to read more about Pissarro’s tough but very inspiring life at Britannica.
                    </div>

                    <div className="post_quote">
                    “Everything is beautiful, all that matters is to be able to interpret.” - Camille Pissarro
                    </div>

                    <div className="post_content">
                    After the first independent exhibition, which was unfortunately rejected by the audience, a French art critic gave the mocking title Impression, Sunrise to his written article critique relating to Monet’s painting. However, since the society existed unnamed, the title was absolutely beneficial. The group of rejected artist finally named their organization as well as the new movement – it officially started the period of impressionists. 
                    </div>

                    <div className="image_container">
                        <img src={quote3} alt="quote3"/>
                    </div>

                    <div className="post_header">
                        <h2>Sound Impression</h2>
                    </div>
    

                    <div className="post_content">
                    Inspired by the paintings and poems, it didn’t take too long for musicians to join the established impressionists. Same characteristics such as, from the one side - tone colors and light, and from the other side - non-traditional and sophisticated musical forms, shaped the highly refined musical compositions ever.  
                    Traditional scales from the Far East, such as pentatonic, and church scales made impressionist music distinctive. Those melodies are combined with complex chord progression in purpose of creating an atmosphere. Above all, performance of impressionist compositions is the main key. In addition to virtuosity, these musical pieces should be played with ease – lightly, sensible and extremely musical
                    </div>

                    <div className="image_container">
                        <img src={newdude} alt="newdude"/>
                    </div>

                    <div className="post_header">
                    <h2>Claude Debussy</h2>
                    </div>

                    <div className="post_content">
                    The reason Debussy is called the father of Impressionism is that he composed musical works like no one ever before him. In addition to being guided by impressionist ideals, his style is very emancipated – Debussy’s musical works are combination of various rhythm patterns and unusual melodies, written in rich and original harmonic and structural forms. His works are magical and are certainly breathtaking! 
                    Although he composed orchestral music, chamber music, choral music, etc., Debussy's works for piano solo are at the top of the list. Debussy definitely made the best use of the piano - virtuosity and sound coloring create different moods and atmosphere. 
                    Among other piano solos, I present you his great collection of Préludes - 24 piano pieces divided in two books. The Book I was published in 1909-10, the Book II 1912-13. One of my favorites Préludes is definitely “The Girl with the Flaxen Hair” from the Book I. Debussy composed it inspired by the poem of the same name by Charles-Marie-René Leconte de Lisle . I recommend to read the poem to fully experience the atmosphere. Music video is down below too.
                    </div>

                    <div className="image_container">
                        <img src={quote1} alt="quote1"/>
                    </div>

                    <div className="post_content">
                    And, one of Debussy’s most famous piano solos - Suite Bergamasque. This work stands out because it’s truly extraordinary - mystical and gentle. Real magic for both the performer and the audience. It is written in the manner of French Baroque and Impressionism. Furthermore, mixture of "old style" and Impressionism was extremely popular in the late 19th century. 
                    The Suite Bergamasque consists of four movements: Prelude, Menuet, Clair de Lune and Passapied. The third movement Clair de Lune, meaning “moonlight”, is the iconic piano piece. It is influenced by the poem of the same name by French poet Paul Verlaine. In my opinion, the best interpretation of the Suite Bergamasque is by the Chilean pianist Claudio Arrau. The whole piece is in the video below.
                    </div>

                        <div className="image_container">
                            <img src={quote4} alt="quote4"/>
                        </div>

                        <div className="post_header">
                            <h2>Erik Satie</h2>
                        </div>
                        <div className="post_content">
                        Erik Satie, known as the father of the avant-garde, made a huge impact on his French contemporaries, including Debussy, as well as on future generations. His influence in music and art is special, mostly because he was never afraid to experiment and be different from others. His nature was very interesting - non-conformist and eccentric with a philosophical and spiritual approach to life. These characteristics also shapes in his works.
                        Erik Satie is a creator of minimalism. Minimalistic musical works are simple repetitive melodies into fragments. Back in his time, this was quite shocking both for the audience and other composers. But such the innovative musical form will be more and more popular, thus future artists, including John Cage and Terry Riley, will later compose great works in the style of minimalism. 
                        Furthermore, Satie invented a new terminology – “Gnossienne”. It represents both a new style and form – composition without a rhythmic form. 6 Gnossiennes is the example of that kind - very minimalistic piano solo pieces written without a rhythmic form. Video below.
                        </div>

                        <div className="image_container">
                        <img src={erik} alt="erik"/>
                        </div>

                        <div className="post_content">
                        In addition to his piano solo works, chamber music,  and other musical forms, Erik Satie added his music to cabares, films and theatres. He collaborated with many other artists. If you’re interested, check out a short piece from the modern ballet “Parade” from 1917. Costumes and set design were designed by Picasso, following the Cocteau’s screenplay. 
                        </div>

                        <div className="post_content">
                        I hope this post will inspire you to listen to other great musical works of Impressionism, or maybe even start learning some of these magical musical works. Do you think it's too complicated? Don’t worry! We can learn together! 😊
                        If you want to discuss more on this topic or you need an advice related to piano - <NavLink to='/contact' exact>
                  contact me
                </NavLink>, because I would love to help you and it's for free! 😊
                        </div>

                        <div className="post_content">
                        <iframe src="https://open.spotify.com/embed/album/1DFixLWuPkv3KT3TnV35m3" width="300" height="380" frameborder="0" allowtransparency="true" allow="encrypted-media"></iframe>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Post1
